body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #282c34;
  color: white;
  min-height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100px;
  padding: 30px 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.list-item {
  padding: 10px 15px;
  cursor: pointer;
  transition: color 0.5s;
}
.list-item:hover {
  color: #61dafb;
}

.live-active {
  color: #61dafb;
}

.width-500 {
  width: 500px;
}
.width-600 {
  width: 600px;
}
.width-700 {
  width: 700px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}
.margin-bottom-30 {
  margin-bottom: 30px;
}
.margin-top-20 {
  margin-top: 20px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}
.inline-flex {
  display: -webkit-inline-flex;
  display: inline-flex;
}
.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-row {
  -webkit-flex-direction: row;
          flex-direction: row;
}
.flex-wrap {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.justify-center {
  -webkit-justify-content: center;
          justify-content: center;
}
.align-center {
  -webkit-align-items: center;
          align-items: center;
}

.font-20 {
  font-size: 20px;
}

.none {
  display: none;
}

.col-61dafb {
  color: #61dafb;
}

.word-wrap {
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
}

.list-li {
  margin-left: 30px;
}

.input-textarea {
  width: 500px;
  height: 200px;
  background-color: #282c34;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  border-radius: 3px;
  resize: none;
  padding: 10px;
}

.input-textarea:focus-visible {
  outline: none;
  border: 1px solid #61dafb;
}

.input-output {
  width: 500px;
  height: 200px;
  word-break: break-all;
  overflow: auto;
  border-radius: 3px;
  border: 1px solid #767676;
  padding: 10px;
  font-size: 10px;
  color: #61dafb;
}

.helper {
  font-weight: 100;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5px 0 10px;
  line-height: 30px;
  background-color: #ffffff;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0px;
}
.left,
.tabs {
  padding: 10px 10px 0px;
  box-sizing: border-box;
  color: #000;
  font-weight: 400;
}
.left,
.clear {
  width: 150px;
}
.left {
  text-align: left;
}
.left b {
  color: #61dafb;
}
.clear {
  text-align: right;
  cursor: pointer;
  border: 1px solid #e3e3e3;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  border-radius: 3px;
  margin-top: 10px;
  color: #b9b9b9;
  font-weight: bold;
  transition-property: text-shadow, box-shadow;
  transition-duration: 0.5s, 1s;
}
.clear:hover {
  text-shadow: 5px 3px 3px;
  box-shadow: 0 0 10px 2px;
}
.tabs {
  width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.tabs > span {
  font-weight: bold;
}
.tabs * {
  display: inline-block;
  padding: 0px 10px;
  cursor: pointer;
}
.tabs *.actived {
  background-color: #61dafb;
  border-radius: 3px;
  color: #fff;
  font-weight: bold;
  transition: background-color 0.5s;
}

@media (max-width: 28.125rem) {
  .helper {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
  }
  .left {
    width: 6.25rem;
    padding: 0;
  }
  .tabs {
    padding: 0;
  }
  .clear {
    position: relative;
    left: calc(100% - 9.7rem);
  }
}
@media (max-width: 18.75rem) {
  .helper {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
  }
  .left {
    padding: 0;
  }
  .tabs {
    padding: 0;
  }
  .clear {
    position: relative;
    left: 0;
  }
}

/* todo-list */
.todo-app {
  opacity: 1;
  max-width: 620px;
  margin: 0px auto;
  box-shadow: 6px 2px 10px #61dafb;
  border-radius: 5px;
  padding: 10px 10px 0px;
  background: #ffffff;
}

@media (max-width: 28.125rem) {
  .todo-app {
    margin: 0 1.25rem;
  }
}

.add-input {
  margin: 0px;
  width: 100%;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4rem;
  border: 0;
  outline: none;
  color: #61dafb;
  padding: 6px;
  border: 1px solid #999;
  box-shadow: inset 0 -1px 5px 0px rgba(0, 0, 0, 0);
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  padding: 16px 16px 16px 16px;
  border: none;
  z-index: 1000;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

@media (max-width: 18.75rem) {
  .add-input {
    font-size: 1rem;
  }
}

/* todo-item */
.todo-item {
  position: relative;
  background: #ffffff;
  font-size: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}
.todo-item:hover .destroy::before {
  opacity: 1;
}
.todo-item label {
  white-space: pre-line;
  word-break: break-all;
  padding: 15px 60px 15px 15px;
  margin-left: 45px;
  display: block;
  line-height: 1.2;
  transition: color 0.4s;
  color: #000;
  font-weight: 500;
}
.finish {
  color: #d9d9d9;
  text-decoration: line-through;
}
.finish label {
  color: #c1c1c1;
}
.toggle {
  text-align: center;
  width: 100%;
  height: 40px;
  position: absolute;
  top: -3px;
  bottom: 0;
  left: 0;
  margin: auto 0;
  border: none;
  outline: none;
  -webkit-appearance: none;
          appearance: none;
}
.toggle:before {
  content: url(data:image/png;base64,);
  position: absolute;
  left: 12px;
  top: 12px;
  cursor: pointer;
}
.toggle:checked:before {
  content: url(data:image/png;base64,);
  position: absolute;
  left: 12px;
  top: 12px;
  cursor: pointer;
}
.destroy {
  position: absolute;
  top: 6px;
  right: 10px;
  margin: auto 0;
  font-size: 30px;
  color: #de0f0f;
  margin-bottom: 11px;
  transition: color 0.2s east-out;
  background-color: transparent;
  -webkit-appearance: none;
          appearance: none;
  border-width: 0;
  cursor: pointer;
  outline: none;
}
.destroy::before {
  content: 'x';
  font-size: 20px;
  opacity: 0;
  transition: opacity 1s;
}
.notPc .destroy::before {
  opacity: 1;
}

